import {
  Box,
  Container,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import React from "react";
import { useMyContext } from "../MyContextProvider";

export default function Features() {
  const theme = useTheme();

  const isMobile = useMediaQuery(theme.breakpoints.down("xs"));
  const isDesktop = useMediaQuery(theme.breakpoints.down("md"));
  const featuresRef = useMyContext().refs.featuresRef;

  return (
    <Box
      sx={{
        py: {
          xs: "80px",
        },
      }}
      ref={featuresRef}
    >
      <Container>
        <Typography
          variant={isDesktop ? "h5" : "h3"}
          fontWeight={"bold"}
          textAlign={"center"}
          gutterBottom
        >
          L'ANALYSE DE CV
        </Typography>
        <Typography paragraph align="center">
          L'analyse de CV est un processus automatis qui extrait et organise
          les informations cls  partir de CV (curriculum vitae). Elle permet
          aux recruteurs de traiter efficacement les candidatures, en
          identifiant rapidement les comptences, l'exprience et l'ducation
          des candidats. Cette technologie acclre le processus de recrutement
          et garantit une meilleure adquation entre les candidats et les postes
          vacants.
        </Typography>
      </Container>
    </Box>
  );
}
